:root {
	--primary: #de354c;
	--primaryLight: #ff7f7f;
	--dark: #932432;
	--accent: #3c1874;
	--light: #f3f3f3;
}

html {
	height: 100%;
	box-sizing: border-box;
}
* {
	box-sizing: inherit;
}
body {
	height: 100%;
	margin: 0;
	background-color: #f3f3f3;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
h1,
h2 {
	color: var(--accent);
}
h1 {
	text-align: center;
}

.button {
	background-color: var(--primary);
	color: white;
	border-width: 0;
	padding: 1.5vh 3vw;
	font-size: 2.5vmin;
	text-decoration: none;
	text-align: center;
	margin: 0;
	cursor: pointer;
}
.bold {
	color: var(--primary);
	font-size: 3.5vmin;
	font-weight: bold;
	cursor: pointer;
}
.emphasis {
	color: var(--accent);
	font-style: italic;
	font-weight: bold;
}
.horizontal {
	display: flex;
	justify-content: space-between;
}
.horizontal-default {
	display: flex;
	flex: 1;
	height: 100%;
	justify-content: center;
	align-items: center;
}
.horizontal-between {
	display: flex;
	flex: 1;
	justify-content: space-between;
	align-items: center;
}
.vertical {
	display: flex;
	flex-direction: column;
}
.italic {
	font-style: italic;
}
.navBarLink {
	color: white;
	text-decoration: none;
	text-align: center;
	cursor: pointer;
	height: 100%;
	font-size: 2.3vh;
}
.navBarHover:hover {
	background-color: var(--dark);
}

#root {
	height: 100%;
}
